'use strict';

/**
* Raised cosine distribution probability density function (PDF).
*
* @module @stdlib/stats-base-dists-cosine-pdf
*
* @example
* import pdf from '@stdlib/stats-base-dists-cosine-pdf' ;
*
* var y = pdf( 2.0, 0.0, 3.0 );
* // returns ~0.083
*
* var myPDF = pdf.factory( 0.0, 3.0 );
* y = myPDF( 2.0 );
* // returns ~0.083
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
